<template>
  <div ref="fakeScroll" class="fake-scroll">
    <div ref="fakeElement" />
  </div>
</template>

<script>
import { computed, onBeforeUnmount, onMounted, ref } from 'vue'

export default {
  name: 'FakeScroll',
  emits: ['down', 'up'],
  setup (_, { emit }) {
    const fakeScroll = ref()
    const fakeElement = ref()

    const scrollHeight = computed(() => {
      if (!fakeScroll.value) return 0
      return fakeElement.value.scrollHeight
    })
    const scrollMiddlePosition = computed(() => {
      return scrollHeight.value / 4
    })

    onMounted(() => {
      setScroll(scrollMiddlePosition.value)

      fakeScroll.value.addEventListener('scroll', updateScroll)
    })

    onBeforeUnmount(() => {
      fakeScroll.value.removeEventListener('scroll', updateScroll)
    })

    const setScroll = (val = 0) => {
      if (!fakeScroll.value) return
      fakeScroll.value.scrollTop = val
    }
    const updateScroll = (e) => {
      console.log(fakeScroll.value.scrollTop)
      if (fakeScroll.value.scrollTop < 1 || e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight) {
        if (fakeScroll.value.scrollTop < scrollMiddlePosition.value) {
          emit('down')
        } else if (fakeScroll.value.scrollTop > scrollMiddlePosition.value) {
          emit('up')
        }
        setScroll(scrollMiddlePosition.value)
      }
    }

    return {
      fakeScroll,
      fakeElement,
      setScroll
    }
  }
}
</script>

<style lang="scss">
.fake-scroll {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  padding: 20px;
  background-color: transparent;
  overflow: scroll;

  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  &::-webkit-scrollbar {
    display: none;
  }

  div {
    height: 150%;
    background-color: transparent;
  }
}
</style>
